html {
  overflow-x: hidden;
}
body {
  margin: 0;
  padding: 0;
  font-family: 'Yanone Kaffeesatz', sans-serif;
  font-size: 14px;
  line-height: 30px;
  font-weight: normal;
}
a {
  text-decoration: none;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-header {
  width: 320px;
  margin: 0 auto;
  padding-top: 10px;
  padding-bottom: 10px;
}
.page-header__wrapper {
  display: flex;
}
.page-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: 'Yanone Kaffeesatz', sans-serif;
}
.page-nav__list {
  display: none;
}
.page-nav__logo {
  margin-top: 10px;
}
.page-nav__btn {
  border: none;
  background-color: #ef7f1a;
  color: white;
  font-family: 'Yanone Kaffeesatz', sans-serif;
  font-size: 18px;
  font-weight: 400px;
  padding: 8px;
  padding-right: 25px;
  position: relative;
}
.page-nav__btn::before {
  position: absolute;
  content: "";
  background-image: url('../../img/icon/strelka.png');
  left: 85px;
  bottom: -1px;
  width: 25px;
  height: 25px;
  background-repeat: no-repeat;
  background-position: 0 0;
}
.user-block {
  display: flex;
  list-style: none;
  width: 50px;
  position: relative;
}
.user-block__link--serch {
  position: relative;
}
.user-block__link--serch::before {
  position: absolute;
  content: "";
  width: 25px;
  height: 25px;
  left: -45px;
  bottom: -12px;
  background-image: url('../../img/icon/search.png');
  background-repeat: no-repeat;
  background-position: 0 0;
}
.user-block__link--basket {
  position: relative;
}
.user-block__link--basket::before {
  position: absolute;
  content: "";
  width: 25px;
  height: 25px;
  left: -20px;
  bottom: -10px;
  background-image: url('../../img/icon/basket.png');
  background-repeat: no-repeat;
  background-position: 0 0;
}
.user-block__btn {
  position: absolute;
  border: none;
  left: 55px;
  top: -38px;
  cursor: pointer;
  width: 40px;
  height: 40px;
  outline: none;
}
.user-block__btn::before {
  position: absolute;
  content: "";
  width: 25px;
  height: 3px;
  background-color: black;
  box-shadow: 0 -6px 0 rgba(0, 0, 0), 0 -12px 0 #000000;
  left: 5px;
  bottom: -4px;
  background-repeat: no-repeat;
  background-position: 0 0;
}
.page-main {
  width: 320px;
  margin: 0 auto;
}
.banner-box-img {
  display: flex;
  justify-content: space-between;
  z-index: 2;
}
.banner-box-img__one {
  background-image: url('../../img/slider/img2.jpg');
  background-repeat: no-repeat;
  width: 321px;
  height: 160px;
}
.banner-box-img__two {
  background-image: url('../../img/slider/img3.jpg');
  background-repeat: no-repeat;
  width: 321px;
  height: 160px;
}
.button-nav {
  background-color: #000000;
  padding: 20px;
}
.button-nav__item {
  list-style: none;
}
.button-nav__list {
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.button-nav__link {
  text-decoration: none;
}
.button-nav__link {
  display: block;
  box-sizing: border-box;
  border-bottom: 1px solid #ffffff;
  width: 200px;
  padding: 10px;
  padding-top: 60px;
  margin-bottom: 10px;
  font-family: 'Yanone Kaffeesatz', sans-serif;
  color: #ffffff;
  font-weight: 400;
  font-size: 24px;
  text-align: center;
}
.button-nav__item {
  position: relative;
}
.button-nav__item::before {
